import React from "react";
import { Form } from "react-bootstrap";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import FormFooter from "./FormFooter";
import styles from "../styles/PostForm.module.css";

const PostForm = ({
  title,
  content,
  handleChange,
  handleSubmit,
  children,
  submitText = "Submit",
  onCancel,
}) => {
  return (
    <Form onSubmit={handleSubmit} className={styles.FormWrapper}>
      <Form.Group controlId="title" className={styles.FormGroupSpacing}>
        <OverlayTrigger
          placement="top"
          overlay={<Tooltip>Enter a short, clear post title</Tooltip>}
        >
          <Form.Control
            type="text"
            placeholder="Enter title"
            name="title"
            value={title}
            onChange={handleChange}
          />
        </OverlayTrigger>
      </Form.Group>

      <Form.Group controlId="content" className={styles.FormGroupSpacing}>
        <OverlayTrigger
          placement="top"
          overlay={<Tooltip>Write the story you want to share</Tooltip>}
        >
          <Form.Control
            as="textarea"
            rows={10}
            placeholder="Write your story..."
            name="content"
            value={content}
            onChange={handleChange}
          />
        </OverlayTrigger>
      </Form.Group>
      {children}
      <FormFooter submitText={submitText} onCancel={onCancel} />
    </Form>
  );
};

export default PostForm;
